import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { a11yDark } from "react-syntax-highlighter/dist/esm/styles/prism";
import { CodeArray } from "./components/CodeSnippets";
import "./App.css";
import Particles from "react-particles-js";
import React from "react";
import {
  FacebookOutlined,
  InstagramOutlined,
  LinkedinOutlined,
} from "@ant-design/icons";

function App() {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        height: "100vh",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <div style={{ position: "absolute" }}>
        <Particles
          width="97vw"
          height="97vh"
          params={{
            particles: {
              move: { speed: 1 },
              color: {
                value: "#333333",
              },
              line_linked: {
                color: {
                  value: "#333333",
                },
              },
              number: {
                value: window.innerWidth > 600 ? 60 : 14,
              },
              size: {
                value: 2,
              },
            },
            interactivity: {
              events: {
                onhover: {
                  enable: true,
                  mode: "repulse",
                },
              },
            },
          }}
        />
      </div>

      <div
        style={{
          display: "flex",
          flexWrap: "wrap",
          flexDirection: "column",
        }}
      >
        <div
          style={{
            display: "flex",
            flexGrow: 1,
            flexWrap: "wrap",
            justifyContent: "center",
          }}
        >
          <div
            style={{
              paddingLeft: 32,
              paddingRight: 64,
              zIndex: 9999,
            }}
          >
            <h1
              style={{
                fontSize: "3em",

                paddingRight: 8,
                paddingLeft: 8,
                borderRadius: 6,
              }}
            >
              Frontend Developer
            </h1>

            <h2
              style={{
                borderLeft: "2px solid #222222",
                padding: 8,
              }}
            >
              Chita Cătălin-Adrian
            </h2>
          </div>

          <div
            style={{
              zIndex: 20000,
            }}
          >
            Random snippets of code i've written
            <div>
              <SyntaxHighlighter language="javascript" style={a11yDark}>
                {CodeArray[0]}
              </SyntaxHighlighter>
            </div>
          </div>
        </div>

        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "flex-start",
            flexGrow: 1,
            marginTop: 16,
          }}
        >
          <a
            href="https://www.linkedin.com/in/c%C4%83t%C4%83lin-adrian-chita-0b9345160/"
            style={{ zIndex: 9999 }}
          >
            <LinkedinOutlined
              style={{ color: "#222222", fontSize: "2em", marginRight: 8 }}
            />
          </a>

          <a
            href="https://www.facebook.com/catalinadrian.chita.1"
            style={{ zIndex: 9999 }}
          >
            <FacebookOutlined
              style={{ color: "#222222", fontSize: "2em", marginRight: 8 }}
            />
          </a>

          <a
            href="https://www.instagram.com/catalin_chita/?hl=en"
            style={{ zIndex: 9999 }}
          >
            <InstagramOutlined style={{ color: "#222222", fontSize: "2em" }} />
          </a>
        </div>
      </div>
      <div
        style={{
          position: "absolute",
          color: "grey",
          zIndex: 9999,
          bottom: 0,
          right: 0,
          padding: 8,
        }}
      >
        <a
          href="https://vincentgarreau.com/particles.js/"
          style={{ textDecoration: "none" }}
        >
          Particle.js
        </a>
      </div>
    </div>
  );
}

export default App;
